* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	height: 100vh;
	font-family: "Zilla Slab", serif;
	display: grid;
	place-items: center;
	background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
		url("./dada.jpg");
}

div {
	position: relative;
	display: grid;
	place-items: center;
	width: 80vw;
	height: 60vh;
	background: rgb(230, 230, 230);
}

.box {
	padding: 20px;
}

.quote {
	font-size: 1.2rem;
	font-weight: 500;
	margin-bottom: 1rem;
}

.worker {
	font-style: italic;
	margin-bottom: 1rem;
}

.logo {
	position: absolute;
	top: -5rem;
	left: 25%;
	color: white;
}

.btn {
	text-transform: capitalize;
	font-family: "Zilla Slab", serif;
	color: black;
}
